import {
  Box,
  Button,
  Grid,
  List,
  ListItem,
  ListItemText,
  Typography,
} from "@mui/material";
import React from "react";
import { useDispatch } from "react-redux";
import { useSearchParams } from "react-router-dom";
import { IncreaseStep, DecreaseStep } from "../../Services/actions/action";
import instance from "../../Utils/api";

const Review = () => {
  const dispatch = useDispatch();
  const [searchParams] = useSearchParams();
  let AppointmentData = JSON.parse(localStorage.getItem("AppointmentData"));
  let UserInformation = JSON.parse(localStorage.getItem("UserInformation"));
  let paidSource = localStorage.getItem("paidSource");
  const [Doctor, setDoctor] = React.useState("");

  React.useEffect(() => {
    const doctor = async () => {
      let res = await instance.post("doctor/doctor", {
        did: searchParams.get("doctor"),
      });
      setDoctor(res.data.doctor);
    };

    doctor();
  }, []);
  const Checkout = async () => {
    try {
      let res = await instance.post("appointment", {
        doctor: searchParams.get("doctor"),
        registrator: "user",
        paidSource: paidSource,
        firstName: AppointmentData.firstName,
        lastName: AppointmentData.lastName,
        age: AppointmentData.age,
        gender: AppointmentData.gender,
        mobileNo: UserInformation?.mobileNo,
        address: AppointmentData.Address,
        city: AppointmentData.city,
        state: AppointmentData.state,
        problem: AppointmentData.problem,
        height: AppointmentData.height,
        weight: AppointmentData.weight,
        amountPaid: Doctor?.fees,
      });
      if (res.status === 200) {
        localStorage.setItem("AppointmentId", res.data.appointment.id);
        dispatch(IncreaseStep(1));
      }
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <React.Fragment>
      <Typography variant="h6" gutterBottom>
        Appointment summary
      </Typography>
      <List disablePadding>
        <ListItem key={`${Doctor?.firstName}`} sx={{ py: 1, px: 0 }}>
          <ListItemText
            primary={`Dr ${Doctor?.firstName} ${Doctor?.lastName}`}
            secondary={`${Doctor?.specialists}`}
          />
          <Typography variant="body2">{`Rs.${Doctor?.fees}`}</Typography>
        </ListItem>
      </List>
      <Grid container spacing={2}>
        <Grid item xs={12} sm={6}>
          <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
            Appointment Address
          </Typography>
          <Typography
            gutterBottom
          >{`${AppointmentData.firstName} ${AppointmentData.lastName}`}</Typography>
          <Typography gutterBottom>{`${UserInformation?.mobileNo}`}</Typography>
          <Typography
            gutterBottom
          >{`${AppointmentData.Address}, ${AppointmentData.city}`}</Typography>
        </Grid>
        <Grid item container direction="column" xs={12} sm={6}>
          <Typography variant="h6" gutterBottom sx={{ mt: 2 }}>
            Payment details
          </Typography>
          <Grid container>
            {paidSource === "offline" ? (
              <Typography gutterBottom>Cash Payment</Typography>
            ) : (
              <Typography gutterBottom>Online Payment</Typography>
            )}
            <Grid item xs={12}>
              <Box sx={{ display: "flex", justifyContent: "flex-end" }}>
                <Button
                  sx={{ mt: 3, ml: 1 }}
                  onClick={() => dispatch(DecreaseStep(1))}
                >
                  Back
                </Button>
                <Button
                  variant="contained"
                  sx={{ mt: 3, ml: 1 }}
                  onClick={Checkout}
                >
                  Make Appointment
                </Button>
              </Box>
            </Grid>
          </Grid>
        </Grid>
      </Grid>
    </React.Fragment>
  );
};

export default Review;
